.toggle {
    display: block;
}

@media screen and (max-width: 550px) {
    .toggle {
        display: none;
    }
}

.nav-active {
    border-bottom: 3px solid white;
}

.footer {
    position: sticky;
    top: 0px;
    z-index: 3;
}

.footer li {
    list-style-type: none;
    color: #fff;
    font-size: 20px;
    font-family: serif;
    margin: 5px;
    text-align: center;
    display: inline-block;
    padding: 10px;
}

.footer li:hover {
    cursor: pointer;
    border-bottom: 3px solid white;
}

.footer-bg {
    background-color: orangered;
}

#a {
    color: white;
    text-decoration: none;
}