.header-title {
    font-family: 'Rajdhani';
    font-size: 42px;
    font-weight: 500;
}

small {
    padding: 10px;
    font-size: 16px;
    color: white;
}

.header-photo {
    width: 100%;
    border: 3px solid white;
}

.header-color {
    color: #e07575;
    opacity: 1;
}

.header-bg {
    background-color: #19191D;
}

.zindex {
    z-index: 1;
}

.header-text {
    padding: 10px;
    position: relative;
    bottom: 0px;
}

#img {
    text-align: center;
    animation: changeBg 10s infinite;
    position: relative;
}

@keyframes changeBg {
    0%,
    100% {
        content: url("../Images/sai-photo.jpg");
    }
    25% {
        content: url("../Images/sai-photo2.jpg");
    }
    50% {
        content: url("../Images/sai-photo.jpg");
    }
    75% {
        content: url("../Images/sai-photo2.jpg");
    }
}