.home-title {
    font-family: Arial;
    font-weight: bold;
}

.home-subtitle {
    font-family: Arial;
    font-weight: 600;
    color: brown;
    background-color: darkgray;
    color: black;
    padding: 5px;
    border-radius: 5px;
}

.home-text {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-stretch: wider;
    text-justify: newspaper;
    font-weight: 400;
}

.required {
    background-color: darkcyan;
    padding: 15px;
    color: white;
    text-shadow: 1px 1px 1px black;
    border-radius: 5px;
    box-shadow: 0px 0px 7px cyan;
}