}
.home-title {
    font-family: Arial;
    font-weight: bold;
}

.home-subtitle {
    font-family: Arial;
    font-weight: 600;
    color: brown;
    background-color: darkgray;
    color: black;
    padding: 5px;
    border-radius: 5px;
}

.home-text {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-stretch: wider;
    text-justify: newspaper;
    font-weight: 400;
}

.required {
    background-color: darkcyan;
    padding: 15px;
    color: white;
    text-shadow: 1px 1px 1px black;
    border-radius: 5px;
    box-shadow: 0px 0px 7px cyan;
}
.mybox {
    background-color: #f1f1f1;
    padding: 20px;
    font-size: 20px;
    text-align: center;
    margin: auto;
    border: 1px solid black;
    box-shadow: 0px 0px 15px silver;
    border-radius: 2%;
    margin: 10px;
}
.Mob-box {
    padding: 10px;
    cursor: pointer;
}

.my-nav-icon {
    cursor: pointer;
    -webkit-animation: navicon 1s infinite;
            animation: navicon 1s infinite;
    font-size: 25px;
}

.my-nav-popup-on {
    position: fixed;
    top: 0;
    right: 0;
    width: 150px;
    height: 100%;
    z-index: 2;
    color: orangered;
    background-color: aliceblue;
    transition: width 2s;
}

.my-nav-popup-off {
    position: fixed;
    display: none;
    top: 0;
    right: 0;
    width: 0px;
    height: 100%;
    z-index: 2;
    color: orangered;
    background-color: aliceblue;
    transition: width 2s;
}

@-webkit-keyframes navicon {
    0%,
    100% {
        box-shadow: 0px 0px 40px #888888;
    }
    25% {
        box-shadow: 0px 0px 10px green;
    }
    50% {
        box-shadow: 0px 0px 20px red;
    }
    75% {
        box-shadow: 0px 0px 30px #888888;
    }
}

@keyframes navicon {
    0%,
    100% {
        box-shadow: 0px 0px 40px #888888;
    }
    25% {
        box-shadow: 0px 0px 10px green;
    }
    50% {
        box-shadow: 0px 0px 20px red;
    }
    75% {
        box-shadow: 0px 0px 30px #888888;
    }
}
