.login-page h3 {
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: brown;
    padding: 5px;
}

.login-page {
    text-align: center;
}

.font-50x {
    font-size: 70px;
    border: 2px solid orangered;
    padding: 10px;
    border-radius: 25%;
    background-color: white;
    color: orangered;
}

.user-box {
    text-align: center;
    position: relative;
    top: -50px;
}

.login-box {
    background-color: orangered;
    margin-top: 30px;
    color: white;
}

.login-box span {
    font-family: monospace;
    font-size: x-large;
    text-align: center;
    display: block;
}