.fa-icon {
    font-size: 25px;
    color: #fff;
    padding: 10px;
    text-align: center;
}

.fa-icon:hover {
    cursor: pointer;
}

.myfooter li {
    list-style-type: none;
    color: #fff;
    font-size: 20px;
    font-family: serif;
    margin: 5px;
    text-align: center;
    display: inline-block;
    padding: 10px;
}

.myfooter li:hover {
    cursor: pointer;
    border-bottom: 3px solid white;
}

.myfooter-bg {
    background-color: orangered;
}