.Mob-box {
    padding: 10px;
    cursor: pointer;
}

.my-nav-icon {
    cursor: pointer;
    animation: navicon 1s infinite;
    font-size: 25px;
}

.my-nav-popup-on {
    position: fixed;
    top: 0;
    right: 0;
    width: 150px;
    height: 100%;
    z-index: 2;
    color: orangered;
    background-color: aliceblue;
    transition: width 2s;
}

.my-nav-popup-off {
    position: fixed;
    display: none;
    top: 0;
    right: 0;
    width: 0px;
    height: 100%;
    z-index: 2;
    color: orangered;
    background-color: aliceblue;
    transition: width 2s;
}

@keyframes navicon {
    0%,
    100% {
        box-shadow: 0px 0px 40px #888888;
    }
    25% {
        box-shadow: 0px 0px 10px green;
    }
    50% {
        box-shadow: 0px 0px 20px red;
    }
    75% {
        box-shadow: 0px 0px 30px #888888;
    }
}